import { useState } from 'react';

type Who = 'chris' | 'kate';

export default function Index() {
	const [who, setWho] = useState<Who | 'none'>('none');

	return (
		<div className="p-8">
			<header className="flex justify-between">
				<h1 className="text-3xl text-emerald-600 font-mono font-medium">
					Bookshelf
				</h1>
				<label>
					Who are you?
					<select
						value={who}
						onChange={e => setWho(e.target.value as Who)}
						className="pb-4"
					>
						<option disabled value="none">
							Please choose
						</option>
						<option value="kate">Kate</option>
						<option value="chris">Chris</option>
					</select>
				</label>
			</header>
		</div>
	);
}
